import request from '@/utils/request'
import store from '@/store'
const CACHE_CHANNEL_UNLOGIN = 'hm-toutiao-01-channel-unlogin'// 游客身份
const CACHE_CHANNEL_LOGIN = 'hm-toutiao-01-channel-login'// 登陆身份
// export function getChannels () {
//   return request({
//     url: '/user/channels',
//     method: 'get'
//   })
// }
// 上面的获取我的频道是直接去调用接口了
// 下面的获取我的频道不用直接调用接口

export function getlocalChannel () {
  return new Promise(async function (resolve, reject) {
    // 获取本地缓存的channel
  // 区分是游客身份还是登陆身份
    var key = null
    // 先去判断是否登陆
    if (store.state.user.token) {
    // 如果有token说明登陆了
      key = CACHE_CHANNEL_LOGIN
    } else {
      key = CACHE_CHANNEL_UNLOGIN
    }
    // console.log(key)
    // 去拿数据
    let str = localStorage.getItem(key)
    if (str) {
      // console.log('if')
      // 如果缓存里有数据，就从缓存中拿转成对象并返回
      resolve(JSON.parse(str))
    } else {
      // 如果没有说明缓存里没有
      // 所以需要从数据库里拿数据
      // console.log('else')
      const data = await request({
        url: '/user/channels'
      })
      // 拿到数据后根据不同的身份存到不同的缓存中
      // console.log(data)
      localStorage.setItem(key, JSON.stringify(data.channels))
      // 存到缓存后再返回
      resolve(data.channels)
    }
  })
}
export function delChannel (channelId) {
  // 判断是否登录
  let key = null
  if (store.state.user.token) {
    // 说明是登录
    key = CACHE_CHANNEL_LOGIN
  } else {
    key = CACHE_CHANNEL_UNLOGIN
  }
  // 有了key以后要去去本地存储里拿数据
  let channels = JSON.parse(localStorage.getItem(key))
  // 从channels中删除channelId
  let index = channels.findIndex(item => item.id === channelId)
  if (index > -1) {
    // 说明找到了
    // 从数组中删除
    channels.splice(index, 1)
    // 然后把它存回本地存储中
    localStorage.setItem(key, JSON.stringify(channels))
  }
}
// 把频道加到本地缓存中
export function addChannel (channel) {
  // 根据是否登录拿到本地存储
  let key = null
  if (store.state.user.token) {
    // 说明是登录
    key = CACHE_CHANNEL_LOGIN
  } else {
    key = CACHE_CHANNEL_UNLOGIN
  }
  // 去本地存储里拿数据
  let channels = JSON.parse(localStorage.getItem(key))
  // 把它推进到channels中
  channels.push(channel)
  // 存回到本地存储中
  localStorage.setItem(key, JSON.stringify(channels))
}
