<template>
  <div class="channel-edit">
    <div class="channel">
      <div class="tit">
        我的频道：
        <span class="tip">点击可进入频道</span>
        <van-button v-if="!editing" @click="editing=true" size="mini" type="info" plain>编辑</van-button>
        <van-button v-else @click="editing=false" size="mini" type="danger" plain>完成</van-button>
      </div>
      <van-grid class="van-hairline--left">
          <!-- 点我的频道可以进入我的频道 -->
          <!-- 编辑频道中显示当前频道 -->
        <van-grid-item v-for="(channel,index) in channels" :key="channel.id" >
          <span class="f12" :class="{red:index===currentChannelIndex}" @click="$emit('changeChannel',channel.id)">{{channel.name}}</span>
          <van-icon class="btn" name="cross" v-show="editing" @click="$emit('delChannel',channel.id)"></van-icon>
        </van-grid-item>
      </van-grid>
    </div>
    <div class="channel">
      <div class="tit">可选频道：</div>
      <van-grid class="van-hairline--left">
        <van-grid-item v-for="item in kexuanArticle" :key="item.id">
          <span class="f12">{{item.name}}</span>
          <van-icon class="btn" name="plus" @click="$emit('addChannel',item)"></van-icon>
        </van-grid-item>
      </van-grid>
    </div>
  </div>
</template>

<script>
import { allArticle } from '@/api/articles'
export default {
  data () {
    return {
      editing: false,
      allArticle: []
    }
  },
  computed: {
    kexuanArticle () {
      return this.allArticle.filter((item) => !this.channels.some((o) => o.id === item.id))
    }
  },
  props: {
    channels: {
      type: Array,
      required: true,
      default: () => []
    },
    currentChannelIndex: {
      type: Number
    }
  },
  created () {
    // 去获取全部频道
    this.getAllArticles()
  },
  methods: {
    async getAllArticles () {
      let data = await allArticle()
      // 得到数据以后
      this.allArticle = data.channels
    }
  }
}
</script>

<style lang='less' scoped>
.channel-edit {
  .channel {
    padding: 10px;
    .tit {
      line-height: 3;
      .tip {
        font-size: 10px;
        color: #999;
      }
    }
    .van-button {
      float: right;
      margin-top: 7px;
    }
    .btn {
      position: absolute;
      bottom: 0;
      right: 0;
      background: #ddd;
      font-size: 12px;
      color: #fff;
    }
    .f12 {
      font-size: 12px;
      color: #555;
    }
    .red {
      color: red;
    }
  }
}
</style>
