<template>
  <div class="container">
    <van-tabs swipeable v-model="activeIndex" @change="changeTab">
      <van-tab :title="item.name" v-for="item in channels" :key="item.id">
        <article-list :channel_id="channels[activeIndex].id" @showAction="showAction"></article-list>
      </van-tab>
    </van-tabs>
    <span class="bar_btn" @click="showChannelEdit=true">
      <van-icon name="wap-nav" />
    </span>
    <van-popup v-model="showMoreAction" :style="{width: '80%'}">
      <more-action @dislike="delArticle($event,'dislike')" @reportArticle="delArticle($event,'report')"></more-action>
    </van-popup>
    <van-action-sheet v-model="showChannelEdit" :round="false" title="编辑频道">
      <channel-edit @addChannel="addChannel" @delChannel="delChannel" :currentChannelIndex="activeIndex" :channels="channels" @changeChannel="changeChannel"></channel-edit>
    </van-action-sheet>
  </div>
</template>

<script>
// import JSONBig from 'json-bigint'
import eventBus from '@/utils/eventBus'
import { disLikeArticle, report } from '@/api/articles'
import MoreAction from './components/more-action'
import ArticleList from './components/article-list'
import { getlocalChannel, delChannel, addChannel } from '@/api/channels'
import ChannelEdit from './components/channel-edit'
export default {
  name: 'home',
  data () {
    return {
      channels: [],
      activeIndex: 0,
      showMoreAction: false,
      channelId: null,
      articleId: null,
      showChannelEdit: false
    }
  },
  components: {
    ArticleList,
    MoreAction,
    ChannelEdit
  },
  created () {
    this.getChannels()
  },
  methods: {
    changeTab () {
      // 当切换tab页时触发
      // console.log('124')
      // 通知所有的article-list告诉他们。我要切换页签了
      // article-list组件需要拿到穿过的页签 看看是否是自己所在的页签
      // 如果是自己所在的页签就需要判断一下，自己的组件是否有滚动数据，如果有滚动数据就设置
      // 触发一个公共事件
      eventBus.$emit('changeTab', this.channels[this.activeIndex].id)
    },
    async getChannels () {
      this.channels = await getlocalChannel()
    },
    showAction (channelId, articleId) {
      // 接收传过来的id
      // console.log(channelId, articleId)
      this.channelId = channelId
      this.articleId = articleId
      this.showMoreAction = true
    },
    /* async dislike () {
      // 在这里调用接口
      try {
        await disLikeArticle({ target: this.articleId })
        this.$gnotify({ type: 'success', message: '操作成功' })
        eventBus.$emit('delArticle', this.channelId, this.articleId)
        this.showMoreAction = false
      } catch (error) {
        this.$gnotify({ type: 'danger', message: '操作失败' })
      }
    },
    async reportArticle (type) {
      // 点击了举报后触发了这个方法
      try {
        // 发送举报请求
        await report({ target: this.articleId, type })
        // 如果成功了的话关闭弹出层
        this.showMoreAction = false
        // 并且将内容从列表中删除
        // 发一个广播让article-list去删除自己的article
        eventBus.$emit('delArticle', this.channelId, this.articleId)
        // 弹出一个成功
        this.$$gnotify({ type: 'success', message: '操作成功' })
      } catch (error) {
        // 弹出一个失败
        this.$$gnotify({ type: 'success', message: '操作失败' })
      }
    } */
    // 把上面这两个方法合并成一个
    async delArticle (params, name) {
      // console.log(params)
      // console.log(name)
      try {
        if (name === 'dislike') {
        // 说明这是不喜欢文章
        // 调用接口
          await disLikeArticle({ target: this.articleId })
        } else {
          await report({ target: this.articleId, type: params })
        }
        this.$gnotify({ type: 'success', message: '操作成功' })
        eventBus.$emit('delArticle', this.channelId, this.articleId)
        this.showMoreAction = false
      } catch (error) {
        this.$gnotify({ type: 'danger', message: '操作失败' })
      }
    },
    changeChannel (channelId) {
      let index = this.channels.findIndex(item => item.id === channelId)
      this.activeIndex = index
      this.showChannelEdit = false
    },
    delChannel (channelId) {
      // console.log(channelId)
      // 删除频道
      // 删除本地存储里的channel
      delChannel(channelId)
      // 删了以后要考虑activeIndex的激活状态

      // 删除this.channels里的channelId
      let index = this.channels.findIndex(item => item.id === channelId)
      if (index > -1) {
        // 如果找到了
        if (index <= this.activeIndex) {
          // 如果小于等于激活状态的话
          // 要把激活的状态改成
          this.activeIndex = this.activeIndex - 1
        }
        // 把它删了
        this.channels.splice(index, 1)
      } else {
        this.$gnotify({ type: 'danger', message: '删除频道失败' })
      }
    },
    addChannel (channel) {
      // 添加频道
      // 把这个加到本地缓存中
      addChannel(channel)
      // 把它加到this.channels中
      this.channels.push(channel)
    }

  }
}
</script>

<style lang="less" scoped>
.van-tabs {
  height: 100%;
  display: flex;
  flex-direction: column;
  /deep/ .van-tabs__wrap {
    height: 36px;
    padding-right: 36px;
    .van-tab {
      line-height: 36px;
    }
    .van-tabs__line {
      background-color: #3296fa;
      height: 2px;
    }
  }
  /deep/ .van-tabs__content {
    flex: 1;
    overflow: hidden;
  }
  /deep/ .van-tab__pane {
    height: 100%;
    .scroll-wrapper {
      height: 100%;
      overflow-y: auto;
    }
  }
}
.bar_btn {
  width: 36px;
  height: 35px;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 0 10px #999;
  /deep/.van-icon-wap-nav {
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #fff;
    font-size: 20px;
    text-align: center;
    line-height: 35px;
  }
}
.van-action-sheet {
  max-height: 100%;
  height: 100%;
  .van-action-sheet__header {
    background: #3296fa;
    color: #fff;
    .van-icon-close {
      color: #fff;
    }
  }
}
</style>
