<template>
  <div class="scroll-wrapper" @scroll="remember" ref="myScroll">
    <van-pull-refresh v-model="downLoading" @refresh="onRefresh" :success-text="refreshSuccessText">
      <van-list v-model="upLoading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <van-cell :to="`/article?articleId=${article.art_id.toString()}`" v-for="article in articles" :key="article.art_id.toString()">
          <div class="article_item">
            <h3 class="van-ellipsis">{{article.title}}</h3>
            <div class="img_box" v-if="article.cover.type===3">
              <van-image class="w33" fit="cover" :src="article.cover.images[0]"></van-image>
              <van-image class="w33" fit="cover" :src="article.cover.images[1]"></van-image>
              <van-image class="w33" fit="cover" :src="article.cover.images[2]"></van-image>
            </div>
            <div class="img_box" v-if="article.cover.type===1">
              <van-image class="w100" fit="cover" :src="article.cover.images[0]"></van-image>
            </div>
            <div class="info_box">
              <span>{{article.aut_name}}</span>
              <span>{{article.comm_count}}评论</span>
              <span>{{article.pubdate | relTime}}</span>
              <span class="close" v-if="user.token" @click.stop="$emit('showAction',channel_id,article.art_id.toString())">
                <van-icon name="cross"></van-icon>
              </span>
            </div>
          </div>
        </van-cell>
      </van-list>
    </van-pull-refresh>

  </div>
</template>

<script>
import eventBus from '@/utils/eventBus'
import { mapState } from 'vuex'
import { getArticles } from '@/api/articles'
export default {
  data () {
    return {
      upLoading: false,
      articles: [],
      finished: false,
      downLoading: false,
      timestamp: null,
      refreshSuccessText: '刷新成功',
      scrollTop: 0 // 用该属性记录仪当前文章实例所滚动的位置
    }
  },
  computed: {
    ...mapState(['user'])
  },
  props: {
    channel_id: {
      type: Number,
      required: true,
      default: null
    }
  },
  created () {
    eventBus.$on('delArticle', (channelId, articleId) => {
      if (this.channel_id === channelId) {
        let index = this.articles.findIndex((item, index) => {
          return item.art_id.toString() === articleId
        })
        if (index > -1) {
          this.articles.splice(index, 1)
        }
      }
    })
    eventBus.$on('changeTab', (id) => {
      // 判断一下id是否等于该组件通过props得到的频道id
      // 如果相等 说明找对了article-list
      // 判断一下自己的组件是否有滚动

      // 表示该文章列表是存在滚动的
      this.$nextTick(() => {
        if (this.scrollTop && this.$refs.myScroll) {
          this.$refs.myScroll.scrollTop = this.scrollTop
        }
      })
    })
  },
  methods: {
    // 定义一个记录位置的方法
    remember (event) {
      // console.log('进入滚动事件了')
      // 当绑定事件只写方法名时，第一个参数就event
      // event.target这个触发事件的元素
      // 记录位置
      this.scrollTop = event.target.scrollTop
    },
    async onLoad () {
      await this.$sleep()
      // 只要进来这个方法就会触发获取数据的接口，并且把新的数据追加到数组中
      // if (this.articles.length < 60) {
      //   let arr = Array.from(Array(20), (value, index) => {
      //     return index + 1
      //   })
      //   console.log('开始加载数据')
      //   // let arr = [1, 2, 3, 4, 5]
      //   // console.log(arr)
      //   this.articles.push(...arr)
      //   this.upLoading = false
      // } else {
      //   this.finished = true
      // }
      // 在上拉加载中加载数据
      // 需要给他传参
      // console.log({ channel_id: this.channel_id, timestamp: this.timestamp || Date.now() })
      const data = await getArticles({ channel_id: this.channel_id, timestamp: this.timestamp || Date.now() })
      // console.log(data)
      this.articles.push(...data.results)

      this.upLoading = false
      if (data.pre_timestamp) {
        this.timestamp = data.pre_timestamp
      } else {
        this.finished = true
      }
    },
    async onRefresh () {
      await this.$sleep()
      // 这里实现下拉刷新
      // let arr = Array.from(Array(20), (value, index) => {
      //   return -index
      // })
      // this.articles.unshift(...arr)
      // this.downLoading = false
      const data = await getArticles({ channel_id: this.channel_id, timestamp: Date.now() })

      this.downLoading = false
      if (data.results.length) {
        // 如果有长度 则需要
        this.articles = data.results
        this.finished = false
        this.timestamp = data.pre_timestamp
        this.refreshSuccessText = `更新了${data.results.length}条数据`
      } else {
        this.refreshSuccessText = '没有更新的数据了'
      }
    }
  },
  activated () {
    // console.log(this.scrollTop)
    // 需要再组件重新激活的时候重新设置原来的滚动条
    // 将原来的位置赋值给原来的滚动条
    if (this.scrollTop && this.$refs.myScroll) {
      this.$refs.myScroll.scrollTop = this.scrollTop
    }
  }
}
</script>

<style lang="less" scoped>
.article_item{
  h3{
    font-weight:normal;
    line-height: 2
  }
  .img_box{
    display:flex;
    justify-content: space-between;
    .w33{
      width:33%;
      height:90px;
    }
    .w100{
      width:100%;
      height:180px;
    }
  }
  .info_box {
    color:#999;
    line-height: 2;
    position:relative;
    font-size: 12px;
    span{
      padding-right:10px;
      &.close{
        border:1px solid #ddd;
        border-radius: 2px;
        text-align: center;
        width:16px;
        height:12px;
        position:absolute;
        line-height: 12px;
        top:7px;
        right:0px;
        font-size:8px;
        padding-right:0;
      }
    }
  }
}
</style>
